import React, {ReactElement} from "react";
import {TeamMemberImage, TeamMemberName, TeamMemberTitle, TeamMemberWrapper} from "./styled";

type TeamMemberCardProps = {
  image: ReactElement;
  name: string;
  jobTitle: string;
};

export const TeamMemberCard = ({ image, name, jobTitle } : TeamMemberCardProps) => {
  return (
    <TeamMemberWrapper>
      <TeamMemberImage>{image}</TeamMemberImage>
      <TeamMemberName>{name}</TeamMemberName>
      <TeamMemberTitle>{jobTitle}</TeamMemberTitle>
    </TeamMemberWrapper>
  );
};
