import React, { PropsWithChildren } from "react";
import { LinkElementInternal, LinkElementExternal } from "./styled";
import RightArrow from "../Icons/Arrows/RightArrow";
import { colors } from "../../../utils/colors";

type LinkProps = PropsWithChildren<{
  address: string;
  arrow?: boolean;
  external?: boolean;
  color?: string;
}>;

const CustomLink = ({ address, arrow, children, external, color }: LinkProps) => {
 
  if (external) {
    return <LinkElementExternal href={address} target="_blank" id="kek" color={color}>
      {children}
      {arrow && <RightArrow color={color ?? colors.primary.blue} />}
    </LinkElementExternal>;
  }

  return (
    <LinkElementInternal to={address} color={color}>
      {children}
      {arrow && <RightArrow color={colors.primary.blue} />}
    </LinkElementInternal>
  );
};

export default CustomLink;
